import React, {
  useContext,
  useEffect,
  useCallback,
  useState,
  useMemo,
  useRef,
} from "react";
import {
  SpaceVertical,
  Paragraph,
  Heading,
  Span,
  Spinner,
  MessageBar,
} from "@looker/components";
import { ExtensionContext40 } from "@looker/extension-sdk-react";

import {
  ForgeThemeProvider,
  Table,
  TableCell,
  TableHeader,
  TableHeaderRow,
  TableRow,
} from "kubra-ux-forge";

import styled from "styled-components";

const StyledTableCell = styled(TableCell)`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 75ch;
`;

const StyledLoadingOverlayDiv = styled.div`
  z-index: 1;
  pointer-events: auto;
  background: #eceef0;
  opacity: 0.8;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Roboto, "Noto Sans JP", "Noto Sans CJK KR", "Noto Sans Arabic UI",
    "Noto Sans Devanagari UI", "Noto Sans Hebrew", "Noto Sans Thai UI",
    "Helvetica", "Arial", sans-serif;
  cursor: default;
`;

const StyledOverlayCenterDiv = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Roboto, "Noto Sans JP", "Noto Sans CJK KR", "Noto Sans Arabic UI",
    "Noto Sans Devanagari UI", "Noto Sans Hebrew", "Noto Sans Thai UI",
    "Helvetica", "Arial", sans-serif;

  & span {
    background: #f6f8fa;
    font-size: 20px;
    font-weight: 100;
  }
`;

export const LoadingOverlay = () => {
  return (
    <StyledLoadingOverlayDiv>
      <StyledOverlayCenterDiv>
        <Spinner />
        <Span>Loading Data...</Span>
      </StyledOverlayCenterDiv>
    </StyledLoadingOverlayDiv>
  );
};

export const VisualizationTile = ({ config }) => {
  const extensionContext = useContext(ExtensionContext40);

  console.log("extensionContext", extensionContext);

  const {
    coreSDK,
    visualizationData,
    visualizationSDK,
    extensionSDK,
    tileSDK,
  } = extensionContext;

  console.log(
    "visualizationData?.queryResponse",
    visualizationData?.queryResponse,
  );

  // console.log("tileSDK?.tileHostData?.queryId", tileSDK?.tileHostData?.queryId);
  // console.log(
  //   "tileSDK?.tileHostData?.querySlug",
  //   tileSDK?.tileHostData?.querySlug,
  // );

  useEffect(() => {
    if (visualizationSDK) {
      visualizationSDK.configureVisualization({});
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const renderComplete = useCallback(() => {
    if (visualizationData) {
      extensionSDK.rendered();
    }
  }, [visualizationData, extensionSDK]);

  const updateMessage = (message, intent = "inform") => {
    setIntent(intent);
    setMessage(message);
  };

  const [intent, setIntent] = useState();
  const [message, setMessage] = useState();
  const [loading, setLoading] = useState(false);

  const [queryDimensions, setQueryDimensions] = useState();
  const [queryData, setQueryData] = useState();

  // Every time the queryResponse.parent_id changes update the data.
  useEffect(() => {
    if (visualizationData?.queryResponse?.data) {
      setQueryData(visualizationData?.queryResponse?.data);
      setQueryDimensions(visualizationData?.queryResponse?.fields?.dimensions);
    }
  }, [visualizationData?.queryResponse?.parent_id]);

  const [currentQueryTaskId, setCurrentQueryTaskId] = useState();

  const handleSort = async (index, direction) => {
    try {
      console.log(index, direction);

      setLoading(true);
      setMessage(undefined);

      const sortColumn = queryDimensions[index]?.name;
      console.log("sortColumn", sortColumn);

      const sortDirection = direction === 1 ? "asc" : "desc";
      console.log("sortDirection", sortDirection);

      // Get query info from visualization data's query task ID
      // OR
      // From the current querySlug.

      const queryTaskId =
        currentQueryTaskId || visualizationData?.queryResponse?.parent_id;
      console.log("queryTaskId", queryTaskId);

      const queryTask = await coreSDK.ok(coreSDK.query_task(queryTaskId));
      console.log("queryTask", queryTask);

      const inlineQueryRequest = {
        ...queryTask.query,
        sorts: [`${sortColumn} ${sortDirection}`],
      };

      delete inlineQueryRequest.analysis_config;
      delete inlineQueryRequest.client_id;
      delete inlineQueryRequest.can;
      delete inlineQueryRequest.expanded_share_url;
      delete inlineQueryRequest.quick_calcs;
      delete inlineQueryRequest.slug;
      delete inlineQueryRequest.share_url;
      delete inlineQueryRequest.url;

      console.log("inlineQueryRequest", inlineQueryRequest);

      const inlineQueryResponse = await coreSDK.ok(
        coreSDK.run_inline_query({
          body: inlineQueryRequest,
          result_format: "json_detail",
        }),
      );

      console.log("inlineQueryResponse", inlineQueryResponse);

      setQueryData(inlineQueryResponse?.data);
      setCurrentQueryTaskId(inlineQueryResponse?.id);
      updateMessage("Data loaded");
    } catch (error) {
      console.error(error);
      updateMessage("Failed to load data", "critical");
    } finally {
      setLoading(false);
    }
  };

  const [initialSortState, setInitialSortState] = useState({
    sortColumn: "",
    sortDirection: "inactive",
  });

  if (visualizationData?.queryResponse?.sorts?.length > 0) {
    let sortColumn = "";
    let sortDirection = "inactive";

    const sorts = visualizationData?.queryResponse?.sorts[0]?.split(" ");

    if (sorts?.length > 1) {
      sortColumn = sorts[0];
      sortDirection = sorts[1];

      setInitialSortState({ sortColumn, sortDirection });
    }
  }

  const tableHeaders = useMemo(
    () =>
      queryDimensions?.map((dimension) => (
        <TableHeader
          key={dimension.name}
          variant="sort"
          text={dimension.label_short}
          defaultSortDirection={
            initialSortState.sortColumn === dimension.name
              ? initialSortState.sortDirection
              : "inactive"
          }
        />
      )),
    [queryDimensions, initialSortState],
  );

  // console.log('tableHeaders', tableHeaders)

  const tableRows = queryData?.map((row, index) => (
    <TableRow id={`row-${index}`} key={`row-${index}`} hasHover>
      {queryDimensions?.map((dimension) => (
        <StyledTableCell
          key={`${dimension.name}-cell-${index}`}
          variant="text"
          text={row[dimension.name].value}
        />
      ))}
    </TableRow>
  ));

  // console.log('tableRows', tableRows)

  // Note the height of 100% on space vertical. All of the divs that are
  // parents of the visualization need to be given a height of 100%
  // in order for the visualization to utilize for full size of the
  // IFRAME content window,
  return (
    <ForgeThemeProvider>
      {/* <SpaceVertical p="xxxxxlarge" width="100%" align="center" height="100%"> */}

      {loading && <LoadingOverlay />}
      {/* <LoadingOverlay /> */}

      {/* {message && <MessageBar intent={intent}>{message}</MessageBar>} */}

      {tableHeaders?.length > 0 ? (
        <Table variant={"striped"}>
          <TableHeaderRow id={"header"} onSort={handleSort}>
            {tableHeaders}
          </TableHeaderRow>
          <>{tableRows}</>
        </Table>
      ) : null}

      {/* <Paragraph>No results</Paragraph> */}
      {/* </SpaceVertical> */}
    </ForgeThemeProvider>
  );
};
